import styled from "styled-components";
import sprite_01 from "@/assets/img/sprite_01.png";

export const HeaderWrapper = styled.div`
  height: 75px;
  background-color: #242424;
  font-size: 14px;

  .content {
    height: 70px;
    display: flex;
    justify-content: space-between;
  }

  .divider {
    height: 5px;
    background-color: #c20c0c;
  }
`;

export const HeaderLeft = styled.div`
  display: flex;
  .logo {
    display: block;
    width: 176px;
    height: 69px;
    background-position: 0 0;
    text-indent: -99999px;
  }

  .select-list {
    display: flex;
    line-height: 70px;

    .select-item {
      position: relative;

      a {
        display: block;
        padding: 0 19px;
        /* padding: 0 38px; */
        color: #ccc;
      }

      /* 为最后一个标签添加 new 小标签 */
      :last-of-type a {
        position: relative;
        /* 红色new，需要绝对定位到标签的右上角位置 */
        /* 设置一个伪元素 */
        ::after {
          position: absolute;
          /* before和after伪元素要content填充一下 */
          content: "";
          width: 28px;
          height: 19px;
          /* 精灵图要用require引入 */
          /* background-image: url(${require("@/assets/img/sprite_01.png")}); */
          background-image: url(${sprite_01});
          background-position: -190px 0;
          top: 20px;
          right: -15px;
        }
      }

      &:hover a,
      a.active {
        color: #fff;
        background: #000;
        text-decoration: none;
      }

      .active .icon {
        position: absolute;
        display: inline-block;
        width: 12px;
        height: 7px;
        bottom: -1px;
        left: 50%;
        transform: translate(-50%, 0);
        background-position: -226px 0;
      }
    }
  }
`;
export const HeaderRight = styled.div`
  display: flex;
  align-items: center;
  color: #ccc;
  font-size: 12px;

  .search {
    width: 158px;
    height: 32px;
    border-radius: 16px;

    input {
      &::placeholder {
        font-size: 12px;
      }
    }
  }

  .center {
    width: 90px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    border: 1px solid #666;
    border-radius: 16px;
    margin: 0 14px;
  }

  .LoginSmall {
    padding-left: 4px;
    padding-right: 26px;
    color: #787878;
  }
`;
